import React from "react";

import colors from "styles/colors";
import CompanyTypes from "utils/companyTypes";

import Layout from "components/layout";
import LogoImageHero from "components/LogoImageHero";
import Title from "components/customer-case-study/Title";
import Sidebar from "components/customer-case-study/Sidebar";
import Body from "components/customer-case-study/Body";
import Bottom from "components/customer-case-study/Bottom";
import { stripLineSeparators } from "utils/sanitizers";

import { ContentOuter, ContentInner } from "./elements";

const categories = {
  Ecommerce: CompanyTypes.ECOMMERCE,
  "Life sciences": CompanyTypes.LIFESCIENCE,
  "Tech startup": CompanyTypes.STARTUP,
};

export default ({ pageContext: { contentfulData }, location, indexPage }) => (
  <Layout
    location={location}
    title={contentfulData.metaTitle}
    description={contentfulData.metaDescription}
    ogImage={contentfulData.thumbnailImage.fields.file.url}
    whiteContentBackground
    whiteGutterBackground
    indexPage={indexPage}
  >
    <LogoImageHero
      logoImage={contentfulData.companyLogo.fields.file.url}
      logoHeight={contentfulData.companyLogoHeight}
      logoWidth={contentfulData.companyLogoWidth}
      backgroundImage={contentfulData.heroBackgroundImage.fields.file.url}
    />
    <ContentOuter>
      <Title customerCategory={contentfulData.customerCategory}>
        {stripLineSeparators(contentfulData.caseStudyTitle)}
      </Title>
      <ContentInner>
        <Sidebar
          brexDifferenceFields={[
            {
              name: "Application Time",
              value: contentfulData.applicationApprovalTime,
            },
            {
              name: "Implementation Time",
              value: contentfulData.implementationTime,
            },
            {
              name: "Pain Points",
              value:
                contentfulData.painPoints &&
                contentfulData.painPoints.join(", "),
            },
            {
              name: "Top Spend Categories",
              value:
                contentfulData.topSpendCategories &&
                contentfulData.topSpendCategories.join(", "),
            },
            {
              name: "Annualized Rewards ROI",
              value: contentfulData.annualizedRewardsRoi,
            },
          ].filter(({ value }) => !!value)}
          companyInfoFields={[
            {
              name: "Founders",
              value:
                contentfulData.founders && contentfulData.founders.join("/n"),
            },
            {
              name: "Founded",
              value: contentfulData.yearFounded,
            },
            {
              name: "Headquarters",
              value: contentfulData.headquarters,
            },
            {
              name: "Employee Count",
              value: contentfulData.employeeCount,
            },
            {
              name: "Funding",
              value: contentfulData.funding,
            },
          ].filter(({ value }) => !!value)}
        />
        <Body
          content={[
            {
              name: "Company",
              type: "bodyText",
              value: contentfulData.bodyCompany,
            },
            {
              name: "Challenge",
              type: "bodyText",
              value: contentfulData.bodyChallenge,
            },
            {
              name: "Quote After Challenge",
              type: "quote",
              value: contentfulData.bodyQuoteAfterChallengeSection,
            },
            {
              name: "Solution",
              type: "bodyText",
              value: contentfulData.bodySolution,
            },
            {
              name: "Quote After Solution",
              type: "quote",
              value: contentfulData.bodyQuoteAfterSolution,
            },
            {
              name: "Outcome",
              type: "bodyText",
              value: contentfulData.bodyOutcome,
            },
            {
              name: "Quote After Outcome",
              type: "quote",
              value: contentfulData.bodyQuoteAfterOutcome,
            },
            {
              name: "Impact",
              type: "bodyText",
              value: contentfulData.bodyImpact,
            },
            {
              name: "Quote After Impact",
              type: "quote",
              value: contentfulData.bodyQuoteAfterImpact,
            },
          ].filter(({ value }) => !!value)}
        />
      </ContentInner>
    </ContentOuter>
    <Bottom
      gutterBackground={colors.offWhite}
      analyticsPath={`customers.${contentfulData.urlPath}.bottom.signup`}
      companyType={categories[contentfulData.customerCategory]}
      refCode={contentfulData.refCode}
    />
  </Layout>
);
