import React from "react";
import { stripLineSeparators } from "utils/sanitizers";
import { Container, Box, Title, Wrapper, Field, Value, Name } from "./elements";

const BoxComponent = ({ title, fields }) => (
  <Box>
    <Title>{title}</Title>
    <Wrapper>
      {fields.map(({ name, value }) => (
        <Field key={name}>
          <Value>{stripLineSeparators(value)}</Value>
          <Name>{name}</Name>
        </Field>
      ))}
    </Wrapper>
  </Box>
);

const Sidebar = ({ brexDifferenceFields, companyInfoFields }) => (
  <Container>
    <BoxComponent title="The Brex Difference" fields={brexDifferenceFields} />
    <BoxComponent title="Company Info" fields={companyInfoFields} />
  </Container>
);

export default Sidebar;
