import React from "react";

import ReactMarkdown from "react-markdown";
import { Container, TextWrapper, Title, Text, Quote } from "./elements";

const TextComponent = ({ name, value }) => (
  <TextWrapper>
    <Title>{name}</Title>
    <Text>
      <ReactMarkdown source={value} />
    </Text>
  </TextWrapper>
);

const QuoteComponent = ({ value }) => <Quote>{value}</Quote>;

const Body = ({ content }) => (
  <Container>
    {content.map(({ name, value, type }) =>
      type === "bodyText" ? (
        <TextComponent key={name} name={name} value={value} />
      ) : (
        <QuoteComponent key={name} value={value} />
      ),
    )}
  </Container>
);

export default Body;
